body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.content {
  min-height: calc(100vh - 70px - 64px);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.loader {
  min-height: calc(100vh - 70px - 64px);
  display: flex;
  justify-content: center;
  margin: 70px auto;
}

.list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 1rem;
}

.card {
  display: flex;
  flex-direction: column;
}

.card-content {
  flex-grow: 1;
}
img,
iframe {
  max-width: 100%;
}
iframe {
  width: 600px;
  height: 350px;
}

.btn{
  color: #260c0c;
  background: radial-gradient(175.86% 2986.62% at 50% 50%, rgba(216, 132, 132, 0.5) 0%, rgba(24, 90, 111, 0.5) 38.85%, rgba(205, 20, 20, 0.5) 100%) /* warning: gradient uses a rotation that is not supported by CSS and may not behave as expected */, #FFFFFF;
}


.fontaw{
  font-size: 1rem;
  color: #795548;
}

h6{
  margin-left: 1rem;
}

.icon-text{
  display: flex !important;
  align-items: baseline;
}

.contactheader{
  margin-top: 0;
  padding-top: 30px;
}
